.library {
  background-color: $main-color;
  background-image: url(../img/book-store/library.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top 0 right -480px;
  padding: 38px 35px 50px;

  @media (min-width: 600px) {
    background-position: center;
  }

  @media (min-width: $screen-m) {
    padding: 65px 80px 75px;
  }

  @media (min-width: $screen-l) {
    padding: 60px 230px 115px;
  }

  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__shape {
    margin-bottom: 40px;
  }

  &__title,
  &__location {
    margin: 0;
    width: 100%;
    text-align: center;
    line-height: 1.7;
    color: $white;
  }

  &__title {
    padding-bottom: 20px;

    @media (min-width: $screen-m) {
      font-size: 30px;
      line-height: 1.4;
    }
  }
}
